function burshReverse () {
  tween({
  duration: 300,
  ease: easing.easeInOut,
}).pipe(interpolate(start, circle))
  .start(shape.set('d'));
}

function burshStart () {
  tween({
  duration: 300,
  ease: easing.easeInOut,
}).pipe(interpolate(circle, start))
  .start(shape.set('d'));
}

osbrush.addEventListener('mouseout', burshStart);
osbrush.addEventListener('mouseover', burshReverse);

var prev = document.querySelector('.whatcustomersay__btnwrap-prev');
var next = document.querySelector('.whatcustomersay__btnwrap-next');

function nextSlide() {
  var slider = document.querySelector('.whatcustomersay__slider');
  var num;
  for(var i = 0; i <= slider.children.length; i++) {
    if(slider.children[i].dataset.check == 'check') {
      num = slider.children[i].id[1];
      break;
    }
  }

  if(num == 3) {
    document.getElementById('s3').removeAttribute('checked');
    document.getElementById('s3').removeAttribute('data-check');
    document.getElementById('s1').dataset.check = 'check';
    document.getElementById('s1').checked = true;
  } else {
    var newNum = Number(num) + 1;
    document.getElementById('s' + num).removeAttribute('checked');
    document.getElementById('s' + num).removeAttribute('data-check');
    document.getElementById('s' + newNum).dataset.check = 'check';
    document.getElementById('s' + newNum).checked = true;
  }
}

function prevSlide() {
  var slider = document.querySelector('.whatcustomersay__slider');
  var num;
  for(var i = 0; i <= slider.children.length; i++) {
    if(slider.children[i].dataset.check == 'check') {
      num = slider.children[i].id[1];
      break;
    }
  }

  if(num == 1) {
    document.getElementById('s1').removeAttribute('checked');
    document.getElementById('s1').removeAttribute('data-check');
    document.getElementById('s3').dataset.check = 'check';
    document.getElementById('s3').checked = true;
  } else {
    var newNum = Number(num) - 1;
    document.getElementById('s' + num).removeAttribute('checked');
    document.getElementById('s' + num).removeAttribute('data-check');
    document.getElementById('s' + newNum).dataset.check = 'check';
    document.getElementById('s' + newNum).checked = true;
  }
}

next.addEventListener('click', nextSlide);
prev.addEventListener('click', prevSlide);

var slides = document.querySelector('.whatcustomersay__slider');

var mc = new Hammer(slides);

// listen to events...
mc.on("swiperight", nextSlide);
mc.on("swipeleft", prevSlide);


var inputName = document.querySelector('.form__form-name');
var inputEmail = document.querySelector('.form__form-email');
var submitButton = document.querySelector('.form__form-subscribe');



submitButton.addEventListener('click', function(e) {
  e.preventDefault();

  if(inputName.value.trim() == '') inputName.style.background = "#ff6b6b";
  if(inputEmail.value.trim().match(/[a-z0-9]+@\w+\.\w+/gi) == null ) inputEmail.style.background = "#ff6b6b";
})

inputName.addEventListener('change', function() {
  if(inputName.value.trim() !== '') inputName.style.background = "#f5fafa";
})

inputEmail.addEventListener('change', function() {
  if(inputEmail.value.trim().match(/[a-z0-9]+@\w+\.\w+/gi) !== null ) inputEmail.style.background = "#f5fafa";
})